@keyframes beat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(1.25);
  }

  15% {
    animation-timing-function: ease-out;
    transform: scale(1.05);
  }

  25% {
    animation-timing-function: ease-in;
    transform: scale(1.15);
  }

  35% {
    animation-timing-function: ease-out;
    transform: scale(1.05);
  }
}

